import type { StateCreator } from "zustand";
import type { AppState, StatsSlice, Notification } from "../types";
import { generateId } from "../helpers";
import { POINTS_CONFIG } from "@/lib/storeConfig";
import { foodItems, musicItems, getRandomItem, InventoryItem } from "@/lib/items";
import { notifyT } from "@/lib/i18n";

export const createStatsSlice: StateCreator<AppState, [], [], StatsSlice> = (set, get) => ({
  points: 0,
  totalPointsEarned: 0,
  appOpenAccumulatedMs: 0,
  lastAppOpenTimestamp: null,
  chatMessageCount: 0,
  giftGivenCount: 0,
  totalUsageMinutes: 0,
  consecutiveDays: 0,
  lastActiveDate: null,
  notifications: [],

  addPoints: (amount) => {
    set((state) => ({ points: state.points + amount, totalPointsEarned: state.totalPointsEarned + amount }));
    get().checkAllChallenges();
  },

  spendPoints: (amount) => {
    const current = get().points;
    if (current < amount) return false;
    set({ points: current - amount });
    return true;
  },

  updateAppOpenTime: () => {
    const now = Date.now();
    const last = get().lastAppOpenTimestamp;
    if (last) {
      const elapsed = now - last;
      if (elapsed > 0 && elapsed < 5 * 60 * 1000) {
        const newAccumulated = get().appOpenAccumulatedMs + elapsed;
        const hourMs = 60 * 60 * 1000;
        const previousHours = Math.floor(get().appOpenAccumulatedMs / hourMs);
        const newHours = Math.floor(newAccumulated / hourMs);
        if (newHours > previousHours) {
          const hoursToAward = newHours - previousHours;
          get().addPoints(hoursToAward * POINTS_CONFIG.APP_OPEN_HOUR);
          get().addNotification(
            notifyT().pointsForStayingActive(hoursToAward * POINTS_CONFIG.APP_OPEN_HOUR),
            "points",
          );
        }
        set({ appOpenAccumulatedMs: newAccumulated });
      }
    }
    set({ lastAppOpenTimestamp: now });
  },

  incrementUsageTime: () => {
    set((state) => ({ totalUsageMinutes: state.totalUsageMinutes + 1 }));
    get().checkAllChallenges();
  },

  updateConsecutiveDays: () => {
    const today = new Date().toDateString();
    const lastActive = get().lastActiveDate;
    if (lastActive === today) return;
    const yesterday = new Date();
    yesterday.setDate(yesterday.getDate() - 1);
    if (lastActive === yesterday.toDateString()) {
      const newStreak = get().consecutiveDays + 1;
      set({ consecutiveDays: newStreak, lastActiveDate: today });
      get().checkAllChallenges();
    } else {
      set({ consecutiveDays: 1, lastActiveDate: today });
    }
  },

  addNotification: (message, type) => {
    const notification: Notification = { id: generateId(), message, type };
    set((state) => ({ notifications: [...state.notifications, notification] }));
    setTimeout(() => {
      get().removeNotification(notification.id);
    }, 3000);
  },

  removeNotification: (id) => set((state) => ({ notifications: state.notifications.filter((n) => n.id !== id) })),

  purchaseRandomBox: (type) => {
    const prices = { emoji: 100, food: 300, music: 300 };
    const price = prices[type];
    if (get().points < price) return { success: false, message: `Not enough points! Need ${price} points.` };
    if (type === "emoji") {
      const result = get().unlockRandomEmoji();
      if (result.success) {
        get().spendPoints(price);
        return { success: true, message: `Unlocked ${result.emoji}!` };
      }
      return { success: false, message: result.message };
    } else if (type === "food") {
      const itemDef = getRandomItem(foodItems);
      const newItem: InventoryItem = {
        id: `${itemDef.id}-${Date.now()}`,
        name: itemDef.name,
        icon: itemDef.icon,
        category: itemDef.category,
        receivedAt: Date.now(),
      };
      get().spendPoints(price);
      set((state) => ({ inventory: [...state.inventory, newItem] }));
      return { success: true, message: `Got ${itemDef.icon} ${itemDef.name}!` };
    } else if (type === "music") {
      const itemDef = getRandomItem(musicItems);
      const newItem: InventoryItem = {
        id: `${itemDef.id}-${Date.now()}`,
        name: itemDef.name,
        icon: itemDef.icon,
        category: itemDef.category,
        receivedAt: Date.now(),
      };
      get().spendPoints(price);
      set((state) => ({ inventory: [...state.inventory, newItem] }));
      return { success: true, message: `Got ${itemDef.icon} ${itemDef.name}!` };
    }
    return { success: false, message: "Unknown box type" };
  },
});
