import type { StateCreator } from "zustand";
import type { AppState, ChatSlice, Message, SeenReplies } from "../types";
import { initialEmojis, getTypingDelay } from "../constants";
import { generateId } from "../helpers";
import { splitMessage } from "@/lib/multiBubbleMessage";
import { processEmojis } from "@/lib/emotionEngine";
import { POINTS_CONFIG, EMOJI_SELECTION_LIMIT } from "@/lib/storeConfig";
import { notifyT } from "@/lib/i18n";

export const createChatSlice: StateCreator<AppState, [], [], ChatSlice> = (set, get) => ({
  messages: [],
  chatCreatedAt: 0,

  addMessage: (content, isUser) => {
    const newMessage: Message = { id: generateId(), content, isUser, timestamp: new Date() };
    set((state) => ({ messages: [...state.messages, newMessage] }));
    if (isUser) {
      set((state) => ({ chatMessageCount: state.chatMessageCount + 1 }));
      get().addPoints(POINTS_CONFIG.CHAT_MESSAGE);
      get().markDailyAction("conversation");
    }
  },

  addScriptedMessage: (content, isUser) => {
    if (isUser) {
      // User scripted message - add immediately with isScripted flag
      const newMessage: Message = {
        id: generateId(),
        content,
        isUser: true,
        timestamp: new Date(),
        isScripted: true,
      };
      set((state) => ({ messages: [...state.messages, newMessage] }));
    } else {
      // Character scripted message - add with typing animation
      const typingId = Date.now().toString();
      const typingMessage: Message = {
        id: typingId,
        content: "...",
        isUser: false,
        timestamp: new Date(),
        isTyping: true,
        isScripted: true,
      };
      set((state) => ({ messages: [...state.messages, typingMessage] }));
      setTimeout(
        () => {
          set((state) => ({
            messages: state.messages.map((m) => (m.id === typingId ? { ...m, content, isTyping: false } : m)),
          }));
        },
        400 + Math.random() * 300,
      );
    }
  },

  addCharacterMessage: (content: string) => {
    const segments = splitMessage(content);

    const showSegment = (index: number) => {
      if (index >= segments.length) {
        // All segments shown, notify if not on chat screen
        if (get().currentScreen !== "chat") {
          set({ hasUnreadMessages: true });
          get().addNotification(notifyT().newMessageFrom(get().characterProfile.name), "message");
        }
        return;
      }

      const typingId = (Date.now() + index).toString();
      const typingMessage: Message = {
        id: typingId,
        content: "...",
        isUser: false,
        timestamp: new Date(),
        isTyping: true,
      };
      set((state) => ({ messages: [...state.messages, typingMessage] }));

      setTimeout(() => {
        set((state) => ({
          messages: state.messages.map((m) =>
            m.id === typingId ? { ...m, content: segments[index], isTyping: false } : m
          ),
        }));
        // Show next segment after a short delay
        if (index < segments.length - 1) {
          setTimeout(() => showSegment(index + 1), 300);
        } else {
          // Final segment - notify
          if (get().currentScreen !== "chat") {
            set({ hasUnreadMessages: true });
            get().addNotification(notifyT().newMessageFrom(get().characterProfile.name), "message");
          }
        }
      }, getTypingDelay());
    };

    showSegment(0);
  },

  addSystemMessage: (content: string) => {
    const systemMessage: Message = {
      id: generateId(),
      content,
      isUser: false,
      timestamp: new Date(),
      isSystem: true,
    };
    set((state) => ({ messages: [...state.messages, systemMessage] }));
  },

  clearMessages: () => {
    set({ messages: [], chatCreatedAt: Date.now() });
  },

  hasUnreadMessages: false,
  setHasUnreadMessages: (value) => set({ hasUnreadMessages: value }),

  emojis: initialEmojis,
  unlockEmoji: (emoji) => {
    set((state) => ({ emojis: state.emojis.map((e) => (e.emoji === emoji ? { ...e, unlocked: true } : e)) }));
    get().addNotification(notifyT().newEmojiUnlocked(emoji), "emoji");
    get().checkAllChallenges();
  },

  unlockRandomEmoji: () => {
    const lockedEmojis = get().emojis.filter((e) => !e.unlocked);
    if (lockedEmojis.length === 0) return { success: false, message: "All emojis already unlocked!" };
    const randomEmoji = lockedEmojis[Math.floor(Math.random() * lockedEmojis.length)];
    get().unlockEmoji(randomEmoji.emoji);
    return { success: true, emoji: randomEmoji.emoji, message: `Unlocked ${randomEmoji.emoji}!` };
  },

  selectedEmojis: [],
  addSelectedEmoji: (emoji) => {
    if (get().selectedEmojis.length >= EMOJI_SELECTION_LIMIT) {
      // Silently reject - shake animation handled in EmojiPanel
      return false;
    }
    set((state) => ({ selectedEmojis: [...state.selectedEmojis, emoji] }));
    return true;
  },
  removeSelectedEmoji: (index) => set((state) => ({ selectedEmojis: state.selectedEmojis.filter((_, i) => i !== index) })),
  clearSelectedEmojis: () => set({ selectedEmojis: [] }),

  sendEmojis: () => {
    const emojis = get().selectedEmojis;
    if (emojis.length === 0) return;
    const tone = get().getCharacterTone();
    const result = processEmojis(
      emojis,
      { playerName: get().playerProfile.name, characterName: get().characterProfile.name },
      tone,
    );
    // Affinity: +1 per conversation (one send), NOT per emoji
    const affinityGain = 1;
    const newMessage = { id: generateId(), content: emojis.join(""), isUser: true, timestamp: new Date() };
    set((state) => ({ messages: [...state.messages, newMessage], chatMessageCount: state.chatMessageCount + 1 }));
    get().addPoints(POINTS_CONFIG.CHAT_MESSAGE);
    get().markDailyAction("conversation");
    if (result.reply) {
      // Track seen reply for Codex before rendering (use original template)
      if (result.replyType && result.originalTemplate) {
        get().markReplySeen(result.replyType, result.originalTemplate);
      }
      // Use shared addCharacterMessage which handles <split> multi-bubble
      get().addCharacterMessage(result.reply);
    }
    get().addAffinity(affinityGain);
    get().clearSelectedEmojis();
  },

  // Codex - seen replies tracking
  seenReplies: {
    single: [],
    mixed: [],
    reversal: [],
    autoMessages: [],
  },
  markReplySeen: (category, reply) => {
    set((state) => {
      const seenList = state.seenReplies[category];
      if (seenList.includes(reply)) return state; // Already seen
      return {
        seenReplies: {
          ...state.seenReplies,
          [category]: [...seenList, reply],
        },
      };
    });
  },
});
