import type { StateCreator } from "zustand";
import type { AppState, CharacterSlice } from "../types";
import { initialCharacterProfile } from "../constants";
import { getZodiacSign, AFFINITY_THRESHOLDS, ZodiacSign } from "@/lib/zodiac";
import { zodiacPreferences } from "@/lib/zodiacPreferences";
import { ToneKey, getZodiacUnlockMessagesForTone } from "@/lib/toneCatalogs";
import { notifyT } from "@/lib/i18n";

export const createCharacterSlice: StateCreator<AppState, [], [], CharacterSlice> = (set, get) => ({
  characterProfile: initialCharacterProfile,
  setCharacterName: (name) => set((state) => ({ characterProfile: { ...state.characterProfile, name } })),
  setCharacterAvatar: (avatar) => set((state) => ({ characterProfile: { ...state.characterProfile, avatar } })),
  setCharacterBirthday: (birthday) => set((state) => ({ characterProfile: { ...state.characterProfile, birthday } })),
  setCharacterVoiceTone: (tone) => set((state) => ({ characterProfile: { ...state.characterProfile, voiceTone: tone } })),
  
  addAffinity: (amount) => {
    const currentProfile = get().characterProfile;
    const newAffinity = Math.min(100, currentProfile.affinity + amount);
    const newUnlocks = { ...currentProfile.unlocks };
    const zodiac = get().getZodiacSign();
    const tone = get().getCharacterTone();
    const playerName = get().playerProfile.name;
    const unlockMessages = getZodiacUnlockMessagesForTone(tone, zodiac, playerName);
    
    if (!currentProfile.unlocks.food && newAffinity >= AFFINITY_THRESHOLDS.food) {
      newUnlocks.food = true;
      get().addNotification(notifyT().favoriteFoodRevealed(currentProfile.name), "unlock");
      setTimeout(() => {
        get().addCharacterMessage(unlockMessages.food);
      }, 500);
    }
    if (!currentProfile.unlocks.place && newAffinity >= AFFINITY_THRESHOLDS.place) {
      newUnlocks.place = true;
      get().addNotification(notifyT().favoritePlaceRevealed(currentProfile.name), "unlock");
      setTimeout(() => {
        get().addCharacterMessage(unlockMessages.place);
      }, 500);
    }
    if (!currentProfile.unlocks.music && newAffinity >= AFFINITY_THRESHOLDS.music) {
      newUnlocks.music = true;
      get().addNotification(notifyT().favoriteMusicRevealed(currentProfile.name), "unlock");
      setTimeout(() => {
        get().addCharacterMessage(unlockMessages.music);
      }, 500);
    }
    
    set((state) => ({
      characterProfile: { ...state.characterProfile, affinity: newAffinity, unlocks: newUnlocks },
    }));
    get().checkAllChallenges();
  },
  
  getZodiacSign: () => {
    const b = get().characterProfile.birthday;
    if (/^\d{2}-\d{2}$/.test(b)) {
      return getZodiacSign(`2000-${b}`);
    }
    return getZodiacSign(b);
  },
  
  getCharacterTone: () => get().characterProfile.voiceTone || "default",
  
  getEffectiveFavorites: () => {
    const player = get().playerProfile;
    const zodiac = get().getZodiacSign();
    const prefs = zodiac ? zodiacPreferences[zodiac] : null;
    return {
      food: player.favoriteFood || (prefs?.foods?.[0] ?? ""),
      place: player.favoritePlace || (prefs?.places?.[0] ?? ""),
      music: player.favoriteMusic || (prefs?.music?.[0] ?? ""),
    };
  },
});
