import React, { useState, useMemo, useCallback, useEffect, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Send, Lock, X, Plus } from "lucide-react";
import { useAppStore } from "@/stores/appStore";
import { toast } from "sonner";
import AttachmentMenu from "./AttachmentMenu";
import InventoryPanel from "./InventoryPanel";
import StorePanel from "./StorePanel";
import ChallengesPanel from "./ChallengesPanel";
import CodexPanel from "./CodexPanel";
import { getRandomConversation, hasConversations } from "@/lib/customMessages";
import useEmblaCarousel from "embla-carousel-react";
import { EMOJI_SELECTION_LIMIT } from "@/lib/storeConfig";
type ChatPanelMode = "emoji" | "attach" | "inventory" | "store" | "challenges" | "codex";

// Fixed panel height to prevent layout shifts
const PANEL_HEIGHT = 180;
const EMOJI_COLS = 8;
const EMOJI_ROWS = 3;
const EMOJIS_PER_PAGE = EMOJI_COLS * EMOJI_ROWS;
const EmojiPanel: React.FC = () => {
  const {
    emojis,
    selectedEmojis,
    addSelectedEmoji,
    removeSelectedEmoji,
    clearSelectedEmojis,
    sendEmojis,
    inventory,
    giveGift,
    characterProfile,
    canUseFortune,
    useFortune,
    addScriptedMessage,
    clearMessages
  } = useAppStore();
  const [panelMode, setPanelMode] = useState<ChatPanelMode>("emoji");

  const handleToggleButton = () => {
    if (panelMode === "emoji") {
      setPanelMode("attach");
    } else {
      setPanelMode("emoji");
    }
  };
  const handleGiftClick = () => {
    setPanelMode("inventory");
  };
  const handleFortuneClick = () => {
    useFortune();
    setPanelMode("emoji");
  };
  const handleStoreClick = () => {
    setPanelMode("store");
  };
  const handleChallengesClick = () => {
    setPanelMode("challenges");
  };
  const handleCodexClick = () => {
    setPanelMode("codex");
  };
  const isSheetMode = panelMode === "challenges" || panelMode === "codex";

  const handleTalkClick = () => {
    if (!hasConversations()) {
      // toast.info("No saved conversations yet. Create one in Settings > Character > Message.");
      toast.info("   .  >  >  .");
      return;
    }

    const conversation = getRandomConversation();
    if (!conversation) return;

    // Output each line with a delay
    let delay = 0;
    const lineDelay = 800; // ms between lines

    conversation.lines.forEach(line => {
      setTimeout(() => {
        addScriptedMessage(line.text, line.speaker === "user");
      }, delay);
      delay += lineDelay;
    });
    setPanelMode("emoji");
    // Mark daily action for talk feature
    useAppStore.getState().markDailyAction('talk');
  };
  const handleClearChat = () => {
    clearMessages();
    setPanelMode("emoji");
  };
  const handleGiveGift = (itemId: string) => {
    giveGift(itemId);
    if (inventory.length <= 1) {
      setPanelMode("emoji");
    }
  };
  const isToggleActive = panelMode !== "emoji";
  return <div className="bg-emoji-panel border-t border-border">
    <AnimatePresence>
      {selectedEmojis.length > 0 && <motion.div initial={{
        height: 0,
        opacity: 0
      }} animate={{
        height: "auto",
        opacity: 1
      }} exit={{
        height: 0,
        opacity: 0
      }} className="flex items-center gap-2 px-4 py-2 bg-muted/50 overflow-hidden">
        <div className="flex-1 flex flex-wrap gap-1">
          {selectedEmojis.map((emoji, index) => (
            <motion.button
              key={index}
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              whileTap={{ scale: 0.9 }}
              onClick={() => removeSelectedEmoji(index)}
              className="text-2xl p-1 hover:bg-muted rounded-lg relative group"
            >
              {emoji}
              <span className="absolute -top-1 -right-1 w-4 h-4 bg-destructive rounded-full flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity">
                <X className="w-3 h-3 text-destructive-foreground" />
              </span>
            </motion.button>
          ))}
        </div>
        <motion.button whileHover={{
          scale: 1.1
        }} whileTap={{
          scale: 0.9
        }} onClick={clearSelectedEmojis} className="p-2 text-muted-foreground hover:text-destructive">
          <X className="w-5 h-5" />
        </motion.button>
      </motion.div>}
    </AnimatePresence>

    {/* Panel container - fixed height for all except challenges */}

    <div className="transition-all duration-300" style={!isSheetMode ? {
      height: PANEL_HEIGHT,
      overflow: "hidden"
    } : undefined}>
      <AnimatePresence mode="wait">
        {panelMode === "attach" && <motion.div key="attachment" initial={{
          opacity: 0
        }} animate={{
          opacity: 1
        }} exit={{
          opacity: 0
        }} className="h-full">
          <AttachmentMenu onGiftClick={handleGiftClick} onFortuneClick={handleFortuneClick} onStoreClick={handleStoreClick} onChallengesClick={handleChallengesClick} onTalkClick={handleTalkClick} onClearChat={handleClearChat} onCodexClick={handleCodexClick} canUseFortune={canUseFortune()} />
        </motion.div>}
        {panelMode === "store" && <motion.div key="store" initial={{
          opacity: 0
        }} animate={{
          opacity: 1
        }} exit={{
          opacity: 0
        }} className="h-full">
          <StorePanel onClose={() => setPanelMode("emoji")} />
        </motion.div>}
        {panelMode === "challenges" && <motion.div key="challenges" initial={{
          opacity: 0
        }} animate={{
          opacity: 1
        }} exit={{
          opacity: 0
        }}>
          <ChallengesPanel onClose={() => setPanelMode("emoji")} />
        </motion.div>}
        {panelMode === "codex" && <motion.div key="codex" initial={{
          opacity: 0
        }} animate={{
          opacity: 1
        }} exit={{
          opacity: 0
        }} className="h-full">
          <CodexPanel onClose={() => setPanelMode("emoji")} />
        </motion.div>}
        {panelMode === "inventory" && <motion.div key="inventory" initial={{
          opacity: 0
        }} animate={{
          opacity: 1
        }} exit={{
          opacity: 0
        }} className="h-full">
          <InventoryPanel inventory={inventory} onGiveGift={handleGiveGift} />
        </motion.div>}
        {panelMode === "emoji" && <EmojiCarousel emojis={emojis} onEmojiSelect={addSelectedEmoji} />}
      </AnimatePresence>
    </div>

    {/* Bottom row with toggle button and send button - always visible */}
    <div className="flex items-center gap-2 px-[15px] py-[15px] mb-0 pl-[16px] pr-[16px] pb-[24px] pt-px">
      <motion.button whileHover={{
        scale: 1.05
      }} whileTap={{
        scale: 0.95
      }} type="button" onClick={handleToggleButton} aria-label={isToggleActive ? "Close" : "More"} className={`w-12 h-12 rounded-2xl items-center justify-center flex flex-row gap-0 py-0 mx-0 px-[15px] transition-colors ${isToggleActive ? "bg-muted text-foreground" : "bg-muted text-muted-foreground hover:bg-muted/80"}`}>
        <motion.div animate={{
          rotate: isToggleActive ? 45 : 0
        }} transition={{
          duration: 0.2
        }}>
          <Plus className="w-5 h-5" />
        </motion.div>
      </motion.button>

      <motion.button whileHover={selectedEmojis.length > 0 ? {
        scale: 1.02
      } : {}} whileTap={selectedEmojis.length > 0 ? {
        scale: 0.98
      } : {}} onClick={sendEmojis} disabled={selectedEmojis.length === 0} className={`w-full py-3 rounded-2xl flex items-center justify-center gap-2 font-semibold transition-all ${selectedEmojis.length > 0 ? "bg-primary text-primary-foreground shadow-lg" : "bg-muted text-muted-foreground cursor-not-allowed"}`}>
        <Send className="w-5 h-5" />
        <span>Send</span>
      </motion.button>
    </div>
  </div>;
};
// Separate carousel component for emoji grid
interface EmojiCarouselProps {
  emojis: {
    emoji: string;
    unlocked: boolean;
  }[];
  onEmojiSelect: (emoji: string) => boolean;
}
const EmojiCarousel: React.FC<EmojiCarouselProps> = ({
  emojis,
  onEmojiSelect
}) => {
  const [emblaRef, emblaApi] = useEmblaCarousel({
    loop: false,
    dragFree: false,
    containScroll: "trimSnaps"
  });
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [scrollSnaps, setScrollSnaps] = useState<number[]>([]);

  // Split emojis into pages
  const pages = useMemo(() => {
    const result: {
      emoji: string;
      unlocked: boolean;
    }[][] = [];
    for (let i = 0; i < emojis.length; i += EMOJIS_PER_PAGE) {
      result.push(emojis.slice(i, i + EMOJIS_PER_PAGE));
    }
    return result;
  }, [emojis]);
  const onSelect = useCallback(() => {
    if (!emblaApi) return;
    setSelectedIndex(emblaApi.selectedScrollSnap());
  }, [emblaApi]);
  useEffect(() => {
    if (!emblaApi) return;
    setScrollSnaps(emblaApi.scrollSnapList());
    emblaApi.on("select", onSelect);
    onSelect();
    return () => {
      emblaApi.off("select", onSelect);
    };
  }, [emblaApi, onSelect]);
  return <motion.div key="emoji" initial={{
    opacity: 0
  }} animate={{
    opacity: 1
  }} exit={{
    opacity: 0
  }} className="h-full flex flex-col">
    <div className="flex-1 overflow-hidden" ref={emblaRef}>
      <div className="flex h-full">
        {pages.map((page, pageIndex) => <div key={pageIndex} className="flex-[0_0_100%] min-w-0 px-2 py-[8px]">
          <div className="grid grid-cols-8 gap-0 h-full content-start py-[8px] px-[8px]">
            {page.map((emojiData, index) => (
              <motion.button
                key={index}
                whileHover={emojiData.unlocked ? { scale: 1.15 } : {}}
                whileTap={emojiData.unlocked ? { scale: 0.9 } : {}}
                onClick={() => {
                  if (emojiData.unlocked) {
                    onEmojiSelect(emojiData.emoji);
                  }
                }}
                disabled={!emojiData.unlocked}
                className={`relative aspect-square w-full flex items-center justify-center text-[1.75rem] rounded-xl transition-all ${emojiData.unlocked ? "hover:bg-muted active:bg-muted" : "opacity-40 grayscale cursor-not-allowed"}`}
              >
                {emojiData.emoji}
                {!emojiData.unlocked && (
                  <div className="absolute inset-0 flex items-center justify-center">
                    <Lock className="w-3 h-3 text-foreground/60" />
                  </div>
                )}
              </motion.button>
            ))}
          </div>
        </div>)}
      </div>
    </div>

    {/* Page indicators */}
    {scrollSnaps.length > 1 && <div className="flex justify-center gap-1.5 py-0 my-[16px] mx-0">
      {scrollSnaps.map((_, index) => <button key={index} onClick={() => emblaApi?.scrollTo(index)} className={`w-1.5 h-1.5 rounded-full transition-all ${index === selectedIndex ? "bg-primary w-3" : "bg-muted-foreground/30"}`} aria-label={`Go to page ${index + 1}`} />)}
    </div>}
  </motion.div>;
};
export default EmojiPanel;