import type { StateCreator } from "zustand";
import type { AppState, CoreSlice, ScreenType } from "../types";
import { getGreetingForTone, getTimeOfDay } from "@/lib/toneCatalogs";

export const createCoreSlice: StateCreator<AppState, [], [], CoreSlice> = (set, get) => ({
  onboardingCompleted: false,
  setOnboardingCompleted: (completed) => {
    set({ onboardingCompleted: completed });

    //    
    if (!completed) return;

    const state = get();

    //      (: import data )
    if (state.messages.length > 0) return;

    const timeOfDay = getTimeOfDay();
    const tone = state.getCharacterTone(); //   tone    
    const playerName = state.playerProfile.name;

    const greeting = getGreetingForTone(tone, timeOfDay, playerName);

    // Initialize chatCreatedAt first, then add greeting via addCharacterMessage for <split> support
    set({ chatCreatedAt: Date.now() });
    get().addCharacterMessage(greeting);
  },

  currentScreen: "home" as ScreenType,
  setCurrentScreen: (screen) => {
    set({ currentScreen: screen });
    if (screen === "chat") {
      set({ hasUnreadMessages: false });
    }
    get().checkAndGrantHourlyReward();
    get().updateAppOpenTime();
  },
});
