import type { StateCreator } from "zustand";
import type { AppState, ChallengesSlice } from "../types";
import { getInitialDailyStats } from "../constants";
import { isChallengeConditionMet } from "../helpers";
import { CHALLENGES } from "@/lib/challengesConfig";
import {
  generateDailyChallenges,
  needsDailyReset,
  isDailyChallengeComplete,
  getLocalDateString,
  DAILY_CHALLENGE_REWARD,
  DAILY_BONUS_REWARD,
} from "@/lib/dailyChallengesConfig";
import { notifyT } from "@/lib/i18n";

export const createChallengesSlice: StateCreator<AppState, [], [], ChallengesSlice> = (set, get) => ({
  completedChallenges: [],
  dailyChallenges: null,
  dailyStats: getInitialDailyStats(),

  checkAndCompleteChallenge: (challengeId) => {
    const challenge = CHALLENGES.find((c) => c.id === challengeId);
    if (!challenge) return;
    const alreadyCompleted = get().completedChallenges.some((c) => c.id === challengeId);
    if (alreadyCompleted) return;
    if (isChallengeConditionMet(challenge.unlockCondition, get())) {
      set((state) => ({
        completedChallenges: [
          ...state.completedChallenges,
          { id: challengeId, completedAt: Date.now(), claimed: false },
        ],
      }));
      // Auto-claim for now
      get().claimChallengeReward(challengeId);
    }
  },

  claimChallengeReward: (challengeId) => {
    const challenge = CHALLENGES.find((c) => c.id === challengeId);
    if (!challenge) return;
    const completed = get().completedChallenges.find((c) => c.id === challengeId);
    if (!completed || completed.claimed) return;

    const reward = challenge.reward;
    if (reward.type === "coins" && reward.points) {
      get().addPoints(reward.points);
      get().addNotification(notifyT().challengeCompleted(challenge.title, reward.points), "challenge");
    } else if (reward.type === "emoji" && reward.emojiId) {
      get().unlockEmoji(reward.emojiId);
      get().addNotification(notifyT().challengeCompletedEmoji(challenge.title, reward.emojiId), "challenge");
    } else if (reward.type === "item" && reward.itemId) {
      get().addItemToInventory(reward.itemId, reward.itemQuantity || 1);
      get().addNotification(notifyT().challengeCompletedItem(challenge.title), "challenge");
    }

    set((state) => ({
      completedChallenges: state.completedChallenges.map((c) =>
        c.id === challengeId ? { ...c, claimed: true } : c,
      ),
    }));
  },

  checkAllChallenges: () => {
    CHALLENGES.forEach((challenge) => get().checkAndCompleteChallenge(challenge.id));
  },

  initializeDailyChallenges: () => {
    const current = get().dailyChallenges;
    const stats = get().dailyStats;
    const today = getLocalDateString();

    // Reset daily stats if new day
    if (stats.dateStamp !== today) {
      set({
        dailyStats: {
          dateStamp: today,
          giftGivenToday: false,
          customMessageAddedToday: false,
          conversationCountToday: 0,
          pomodoroCountToday: 0,
          todoCompletedToday: 0,
          talkUsedToday: false,
        },
      });
    }

    // Generate new challenges if needed
    if (needsDailyReset(current)) {
      set({ dailyChallenges: generateDailyChallenges() });
    }
  },

  checkAndCompleteDailyChallenges: () => {
    const state = get();
    const dailyChallenges = state.dailyChallenges;
    if (!dailyChallenges) return;

    const todayStats = state.getTodayStats();
    let updated = false;
    let newlyCompleted = 0;

    const updatedChallenges = dailyChallenges.challenges.map((challenge) => {
      if (challenge.completed) return challenge;

      if (isDailyChallengeComplete(challenge.condition, todayStats)) {
        updated = true;
        newlyCompleted++;
        // Award points for completing
        state.addPoints(DAILY_CHALLENGE_REWARD);
        state.addNotification(notifyT().dailyChallengeCompleted(DAILY_CHALLENGE_REWARD), "challenge");
        return { ...challenge, completed: true };
      }
      return challenge;
    });

    if (updated) {
      const allComplete = updatedChallenges.every((c) => c.completed);
      const shouldAwardBonus = allComplete && !dailyChallenges.bonusClaimed;

      set({
        dailyChallenges: {
          ...dailyChallenges,
          challenges: updatedChallenges,
          bonusClaimed: shouldAwardBonus ? true : dailyChallenges.bonusClaimed,
        },
      });

      if (shouldAwardBonus) {
        state.addPoints(DAILY_BONUS_REWARD);
        state.addNotification(notifyT().dailyChallengeBonus(DAILY_BONUS_REWARD), "challenge");
      }
    }
  },

  markDailyAction: (action) => {
    const stats = get().dailyStats;
    const today = getLocalDateString();

    // Reset if new day
    if (stats.dateStamp !== today) {
      set({
        dailyStats: {
          dateStamp: today,
          giftGivenToday: action === "gift",
          customMessageAddedToday: action === "customMessage",
          conversationCountToday: action === "conversation" ? 1 : 0,
          pomodoroCountToday: action === "pomodoro" ? 1 : 0,
          todoCompletedToday: action === "todo" ? 1 : 0,
          talkUsedToday: action === "talk",
        },
      });
    } else {
      set({
        dailyStats: {
          ...stats,
          giftGivenToday: stats.giftGivenToday || action === "gift",
          customMessageAddedToday: stats.customMessageAddedToday || action === "customMessage",
          conversationCountToday: stats.conversationCountToday + (action === "conversation" ? 1 : 0),
          pomodoroCountToday: stats.pomodoroCountToday + (action === "pomodoro" ? 1 : 0),
          todoCompletedToday: stats.todoCompletedToday + (action === "todo" ? 1 : 0),
          talkUsedToday: stats.talkUsedToday || action === "talk",
        },
      });
    }

    // Check if any daily challenges completed
    get().checkAndCompleteDailyChallenges();
  },

  getTodayStats: () => {
    const stats = get().dailyStats;
    const today = getLocalDateString();

    if (stats.dateStamp !== today) {
      return {
        giftGivenToday: false,
        customMessageAddedToday: false,
        conversationCountToday: 0,
        pomodoroCountToday: 0,
        todoCompletedToday: 0,
        talkUsedToday: false,
      };
    }

    return {
      giftGivenToday: stats.giftGivenToday,
      customMessageAddedToday: stats.customMessageAddedToday,
      conversationCountToday: stats.conversationCountToday,
      pomodoroCountToday: stats.pomodoroCountToday,
      todoCompletedToday: stats.todoCompletedToday,
      talkUsedToday: stats.talkUsedToday,
    };
  },
});
