import type { StateCreator } from "zustand";
import type { AppState, InventorySlice } from "../types";
import { generateId, getDateString, isPreferredItem } from "../helpers";
import { HOURLY_REWARD_INTERVAL } from "../constants";
import { InventoryItem, getHourlyRewardItem, findItemById } from "@/lib/items";
import { getGiftReplyForTone, getFortuneForTone } from "@/lib/toneCatalogs";
import { t, notifyT } from "@/lib/i18n";

export const createInventorySlice: StateCreator<AppState, [], [], InventorySlice> = (set, get) => ({
  inventory: [],
  lastRewardTimestamp: null,
  lastFortuneDate: null,

  checkAndGrantHourlyReward: () => {
    const lastReward = get().lastRewardTimestamp;
    const now = Date.now();

    if (lastReward === null) {
      set({ lastRewardTimestamp: now });
      return;
    }

    if (now - lastReward >= HOURLY_REWARD_INTERVAL) {
      const favorites = get().getEffectiveFavorites();
      const itemDef = getHourlyRewardItem(favorites);

      const newItem: InventoryItem = {
        id: `${itemDef.id}-${generateId()}`,
        name: itemDef.name,
        icon: itemDef.icon,
        category: itemDef.category,
        receivedAt: now,
      };

      set((state) => ({
        inventory: [...state.inventory, newItem],
        lastRewardTimestamp: now,
      }));

      get().addNotification(notifyT().receivedGift(itemDef.icon, itemDef.name), "reward");
    }
  },

  removeFromInventory: (itemId: string) => {
    set((state) => ({ inventory: state.inventory.filter((item) => item.id !== itemId) }));
  },

  addItemToInventory: (itemId, quantity = 1) => {
    const itemDef = findItemById(itemId);
    if (!itemDef) return;
    for (let i = 0; i < quantity; i++) {
      const newItem: InventoryItem = {
        id: `${itemDef.id}-${Date.now()}-${i}`,
        name: itemDef.name,
        icon: itemDef.icon,
        category: itemDef.category,
        receivedAt: Date.now(),
      };
      set((state) => ({ inventory: [...state.inventory, newItem] }));
    }
  },

  giveGift: (itemId: string) => {
    const item = get().inventory.find((i) => i.id === itemId);
    if (!item) return;
    const zodiac = get().getZodiacSign();
    const isPreferred = isPreferredItem(item.name, item.category, zodiac);
    const affinityGain = isPreferred ? 8 : 3;
    get().removeFromInventory(itemId);
    get().addAffinity(affinityGain);
    set((state) => ({ giftGivenCount: state.giftGivenCount + 1 }));
    get().checkAllChallenges();
    get().markDailyAction("gift");
    const tone = get().getCharacterTone();
    const playerName = get().playerProfile.name;
    const reply = getGiftReplyForTone(tone, item.name, isPreferred, playerName);
    get().addCharacterMessage(reply);
    get().addSystemMessage(t().giftGiven(item.name, affinityGain));
  },

  canUseFortune: () => {
    const lastDate = get().lastFortuneDate;
    const today = getDateString();
    if (lastDate === today) return false;
    return true;
  },

  useFortune: () => {
    if (!get().canUseFortune()) return null;
    const tone = get().getCharacterTone();
    const playerName = get().playerProfile.name;
    const fortune = getFortuneForTone(tone, playerName);
    const today = getDateString();
    set({ lastFortuneDate: today });
    get().addCharacterMessage(` : ${fortune.label}\n${fortune.message}`);
    return fortune;
  },
});
