import React from 'react';
import { motion } from 'framer-motion';
import StatusBar from './StatusBar';
import NotificationBanner from './NotificationBanner';
import { useAppStore } from '@/stores/appStore';

interface PhoneFrameProps {
  children: React.ReactNode;
}
const PhoneFrame: React.FC<PhoneFrameProps> = ({
  children
}) => {
  const onboardingCompleted = useAppStore((state) => state.onboardingCompleted);

  return <div className="min-h-screen flex items-center justify-center p-4">
    <motion.div initial={{
      scale: 0.9,
      opacity: 0
    }} animate={{
      scale: 1,
      opacity: 1
    }} transition={{
      duration: 0.5,
      ease: 'easeOut'
    }} className="relative w-[375px] h-[812px] bg-phone-frame rounded-phone shadow-phone overflow-hidden">
      {/* Phone bezel/frame */}
      <div className="absolute inset-0 rounded-phone border-[12px] border-phone-frame pointer-events-none z-50" />

      {/* Notch */}
      {/* <div className="absolute top-0 left-1/2 -translate-x-1/2 w-[150px] h-[30px] bg-phone-frame rounded-b-3xl z-50" /> */}

      {/* Screen area */}
      <div className="relative w-full h-full bg-phone-home-bg overflow-hidden rounded-[28px]">
        <StatusBar />
        {onboardingCompleted && <NotificationBanner />}

        {/* Main content */}
        <div className="relative h-[calc(100%-44px)] mt-[44px] overflow-hidden mx-[10px]">
          {children}
        </div>

        {/* Home indicator */}
        <div className="absolute bottom-2 left-1/2 -translate-x-1/2 w-[134px] h-[5px] bg-foreground/20 rounded-full" />
      </div>
    </motion.div>
  </div>;
};
export default PhoneFrame;