import { create } from "zustand";
import { persist } from "zustand/middleware";
import type { AppState } from "./types";
import { initialEmojis } from "./constants";
import { normalizeItemCategory } from "@/lib/items";

import {
  createCoreSlice,
  createPlayerSlice,
  createCharacterSlice,
  createChatSlice,
  createPomodoroSlice,
  createTodoSlice,
  createInventorySlice,
  createChallengesSlice,
  createSettingsSlice,
  createStatsSlice,
} from "./slices";

// Re-export types and helpers for backward compatibility
export * from "./types";
export { generateId } from "./helpers";

export const useAppStore = create<AppState>()(
  persist(
    (...args) => ({
      ...createCoreSlice(...args),
      ...createPlayerSlice(...args),
      ...createCharacterSlice(...args),
      ...createChatSlice(...args),
      ...createPomodoroSlice(...args),
      ...createTodoSlice(...args),
      ...createInventorySlice(...args),
      ...createChallengesSlice(...args),
      ...createSettingsSlice(...args),
      ...createStatsSlice(...args),
    }),
    {
      name: "companion-app-storage",
      partialize: (state) => ({
        onboardingCompleted: state.onboardingCompleted,
        playerProfile: state.playerProfile,
        messages: state.messages,
        chatCreatedAt: state.chatCreatedAt,
        emojis: state.emojis,
        pomodoroSessionsCompleted: state.pomodoroSessionsCompleted,
        pomodoroRecords: state.pomodoroRecords,
        todos: state.todos,
        completedTodoRecords: state.completedTodoRecords,
        showCompletedTodos: state.showCompletedTodos,
        totalUsageMinutes: state.totalUsageMinutes,
        consecutiveDays: state.consecutiveDays,
        lastActiveDate: state.lastActiveDate,
        selectedTheme: state.selectedTheme,
        colorOverrides: state.colorOverrides,
        chatWallpaper: state.chatWallpaper,
        backgroundGradient: state.backgroundGradient,
        characterProfile: state.characterProfile,
        soundEnabled: state.soundEnabled,
        soundVolume: state.soundVolume,
        taskDuration: state.taskDuration,
        breakDuration: state.breakDuration,
        breakEnabled: state.breakEnabled,
        autoStartBreak: state.autoStartBreak,
        inventory: state.inventory,
        lastRewardTimestamp: state.lastRewardTimestamp,
        lastFortuneDate: state.lastFortuneDate,
        points: state.points,
        totalPointsEarned: state.totalPointsEarned,
        appOpenAccumulatedMs: state.appOpenAccumulatedMs,
        lastAppOpenTimestamp: state.lastAppOpenTimestamp,
        chatMessageCount: state.chatMessageCount,
        giftGivenCount: state.giftGivenCount,
        completedChallenges: state.completedChallenges,
        autoMessageEnabled: state.autoMessageEnabled,
        autoMessagePreset: state.autoMessagePreset,
        dailyChallenges: state.dailyChallenges,
        dailyStats: state.dailyStats,
        seenReplies: state.seenReplies,
        language: state.language,
      }),
      merge: (persistedState, currentState) => {
        const persisted = persistedState as Partial<AppState>;

        // Merge emojis: add new emojis from code, preserve user's unlock progress
        const mergedEmojis = initialEmojis.map((codeEmoji) => {
          const savedEmoji = persisted.emojis?.find((e) => e.emoji === codeEmoji.emoji);
          if (savedEmoji) {
            // Preserve user's unlock status for existing emojis
            return { ...codeEmoji, unlocked: savedEmoji.unlocked };
          }
          // New emoji from code - use default unlock status
          return codeEmoji;
        });

        // Migrate inventory items to remove legacy zodiacCategory
        const migratedInventory = (persisted.inventory ?? []).map((item) => normalizeItemCategory(item));

        return {
          ...currentState,
          ...persisted,
          emojis: mergedEmojis,
          inventory: migratedInventory,
        };
      },
    },
  ),
);
