// lib/multiBubbleMessage.ts
// Shared helper for multi-bubble character messages using <split> token

export const SPLIT_DELIMITER = '<split>';

export interface MessageSegment {
  content: string;
  index: number;
}

/**
 * Splits a message by the <split> token.
 * Returns an array of trimmed, non-empty segments.
 * If no <split> token exists, returns a single-element array with the original content.
 */
export function splitMessage(content: string): string[] {
  if (!content.includes(SPLIT_DELIMITER)) {
    return [content];
  }
  
  return content
    .split(SPLIT_DELIMITER)
    .map((s) => s.trim())
    .filter(Boolean);
}

/**
 * Checks if a message contains the <split> delimiter
 */
export function hasMultipleBubbles(content: string): boolean {
  return content.includes(SPLIT_DELIMITER);
}
