import type { StateCreator } from "zustand";
import type { AppState, PlayerSlice } from "../types";
import { initialPlayerProfile } from "../constants";

export const createPlayerSlice: StateCreator<AppState, [], [], PlayerSlice> = (set) => ({
  playerProfile: initialPlayerProfile,
  setPlayerName: (name) => set((state) => ({ playerProfile: { ...state.playerProfile, name } })),
  setPlayerBirthday: (birthday) => set((state) => ({ playerProfile: { ...state.playerProfile, birthday } })),
  setPlayerFavoriteFood: (favoriteFood) => set((state) => ({ playerProfile: { ...state.playerProfile, favoriteFood } })),
  setPlayerFavoritePlace: (favoritePlace) => set((state) => ({ playerProfile: { ...state.playerProfile, favoritePlace } })),
  setPlayerFavoriteMusic: (favoriteMusic) => set((state) => ({ playerProfile: { ...state.playerProfile, favoriteMusic } })),
});
