import React, { useState } from "react";
import { motion } from "framer-motion";
import { X, BookOpen, ChevronUp, ChevronDown } from "lucide-react";
import { useAppStore } from "@/stores/appStore";
import { getActiveCatalog } from "@/lib/toneCatalogs";
import { codexT } from "@/lib/i18n";
import { EmotionTag, IntensityLevel } from "@/lib/emotionEngine/emotion";

interface CodexPanelProps {
  onClose: () => void;
}

interface CategoryProgress {
  label: string;
  seen: number;
  total: number;
}

const COLLAPSED_HEIGHT = 220;
const EXPANDED_HEIGHT = 400;

const CodexPanel: React.FC<CodexPanelProps> = ({ onClose }) => {
  const characterProfile = useAppStore((state) => state.characterProfile);
  const seenReplies = useAppStore((state) => state.seenReplies);
  const [isExpanded, setIsExpanded] = useState(false);

  const tone = characterProfile?.voiceTone || "default";
  const catalog = getActiveCatalog(tone);
  const labels = codexT();

  const toggleExpand = () => setIsExpanded(!isExpanded);

  // Calculate progress for each category
  const getProgress = (): CategoryProgress[] => {
    const progress: CategoryProgress[] = [];

    // 1. Single emotions - count all unique reply strings across all emotions and intensities
    let singleTotal = 0;
    let singleSeen = 0;
    const singleReplies = catalog.chat.single;
    
    Object.keys(singleReplies).forEach((emotion) => {
      const emotionReplies = singleReplies[emotion as EmotionTag];
      if (emotionReplies) {
        Object.keys(emotionReplies).forEach((intensity) => {
          const replies = emotionReplies[Number(intensity) as IntensityLevel] || [];
          singleTotal += replies.length;
          replies.forEach((reply) => {
            if (seenReplies.single.includes(reply)) {
              singleSeen++;
            }
          });
        });
      }
    });
    
    progress.push({
      label: labels.single,
      seen: singleSeen,
      total: singleTotal,
    });

    // 2. Mixed emotions
    let mixedTotal = 0;
    let mixedSeen = 0;
    const mixedReplies = catalog.chat.mixed;
    
    Object.keys(mixedReplies).forEach((pairKey) => {
      const pairReplies = mixedReplies[pairKey];
      if (pairReplies) {
        Object.keys(pairReplies).forEach((intensity) => {
          const replies = pairReplies[Number(intensity) as IntensityLevel] || [];
          mixedTotal += replies.length;
          replies.forEach((reply) => {
            if (seenReplies.mixed.includes(reply)) {
              mixedSeen++;
            }
          });
        });
      }
    });
    
    progress.push({
      label: labels.mixed,
      seen: mixedSeen,
      total: mixedTotal,
    });

    // 3. Reversal
    let reversalTotal = 0;
    let reversalSeen = 0;
    const reversalReplies = catalog.chat.reversal;
    
    Object.keys(reversalReplies).forEach((poolId) => {
      const replies = reversalReplies[poolId] || [];
      reversalTotal += replies.length;
      replies.forEach((reply) => {
        if (seenReplies.reversal.includes(reply)) {
          reversalSeen++;
        }
      });
    });
    
    progress.push({
      label: labels.reversal,
      seen: reversalSeen,
      total: reversalTotal,
    });

    // 4. Auto Messages
    const autoMessages = catalog.autoMessages || [];
    const autoTotal = autoMessages.length;
    const autoSeen = autoMessages.filter((msg) => 
      seenReplies.autoMessages.includes(msg.text)
    ).length;
    
    progress.push({
      label: labels.autoMessages,
      seen: autoSeen,
      total: autoTotal,
    });

    return progress;
  };

  const categories = getProgress();
  const totalSeen = categories.reduce((sum, cat) => sum + cat.seen, 0);
  const totalAll = categories.reduce((sum, cat) => sum + cat.total, 0);

  return (
    <motion.div
      initial={{ height: COLLAPSED_HEIGHT }}
      animate={{ height: isExpanded ? EXPANDED_HEIGHT : COLLAPSED_HEIGHT }}
      transition={{ type: 'spring', stiffness: 300, damping: 30 }}
      className="flex flex-col overflow-hidden"
    >
      {/* Header */}
      <div className="flex items-center gap-2 px-4 pt-4 pb-2">
        <BookOpen className="w-4 h-4 text-primary" />
        <h3 className="font-semibold text-foreground text-sm flex-1">{labels.title}</h3>
        <span className="text-xs text-muted-foreground">({totalSeen}/{totalAll})</span>
        <motion.button 
          whileTap={{ scale: 0.9 }} 
          onClick={toggleExpand} 
          className="p-1.5 rounded-lg hover:bg-muted transition-colors"
        >
          {isExpanded ? (
            <ChevronDown className="w-4 h-4 text-muted-foreground" />
          ) : (
            <ChevronUp className="w-4 h-4 text-muted-foreground" />
          )}
        </motion.button>
        <motion.button
          whileTap={{ scale: 0.9 }}
          onClick={onClose}
          className="p-1.5 rounded-lg hover:bg-muted transition-colors"
        >
          <X className="w-4 h-4 text-muted-foreground" />
        </motion.button>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto overflow-x-hidden px-4 pb-4 space-y-3">
        {/* Total Progress */}
        <div className="bg-primary/10 rounded-xl p-3">
          <div className="flex items-center justify-between mb-2">
            <span className="text-xs font-medium text-foreground">{labels.totalProgress}</span>
            <span className="text-xs font-bold text-primary">
              {totalSeen} / {totalAll}
            </span>
          </div>
          <div className="w-full h-1.5 bg-muted rounded-full overflow-hidden">
            <motion.div
              initial={{ width: 0 }}
              animate={{ width: totalAll > 0 ? `${(totalSeen / totalAll) * 100}%` : "0%" }}
              transition={{ duration: 0.5, ease: "easeOut" }}
              className="h-full bg-primary rounded-full"
            />
          </div>
        </div>

        {/* Category List */}
        {categories.map((category, index) => {
          const percentage = category.total > 0 ? (category.seen / category.total) * 100 : 0;
          
          return (
            <motion.div
              key={category.label}
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.05 }}
              className="bg-muted/50 rounded-xl p-2.5"
            >
              <div className="flex items-center justify-between mb-1">
                <span className="text-xs text-foreground">{category.label}</span>
                <span className="text-[10px] font-medium text-muted-foreground">
                  {category.seen} / {category.total}
                </span>
              </div>
              <div className="w-full h-1 bg-muted rounded-full overflow-hidden">
                <motion.div
                  initial={{ width: 0 }}
                  animate={{ width: `${percentage}%` }}
                  transition={{ duration: 0.4, delay: index * 0.05 }}
                  className="h-full bg-accent rounded-full"
                />
              </div>
            </motion.div>
          );
        })}
      </div>
    </motion.div>
  );
};

export default CodexPanel;
