import type { StateCreator } from "zustand";
import type { AppState, TodoSlice, CompletedTodoRecord } from "../types";
import { generateId, getDateString } from "../helpers";
import { getAffinityTier } from "@/lib/completionReplies";
import { getCompletionReplyForTone } from "@/lib/toneCatalogs";

export const createTodoSlice: StateCreator<AppState, [], [], TodoSlice> = (set, get) => ({
  todos: [],
  showCompletedTodos: false,
  completedTodoRecords: [],

  addTodo: (text) => set((state) => ({ todos: [...state.todos, { id: generateId(), text, completed: false }] })),

  toggleTodo: (id) => {
    const todo = get().todos.find((t) => t.id === id);
    if (todo && !todo.completed) {
      const record: CompletedTodoRecord = {
        id: todo.id,
        text: todo.text,
        completedAt: getDateString(),
        timestamp: Date.now(),
      };
      set((state) => ({
        todos: state.todos.map((t) => (t.id === id ? { ...t, completed: true, completedAt: getDateString() } : t)),
        completedTodoRecords: [...state.completedTodoRecords, record],
      }));
      get().checkAllChallenges();
      get().markDailyAction("todo");
      const affinity = get().characterProfile.affinity;
      const tier = getAffinityTier(affinity);
      const tone = get().getCharacterTone();
      const playerName = get().playerProfile.name;
      const reply = getCompletionReplyForTone(tone, "todo", tier, playerName);
      get().addCharacterMessage(reply);
    } else {
      set((state) => ({
        todos: state.todos.map((t) => (t.id === id ? { ...t, completed: false, completedAt: undefined } : t)),
      }));
    }
  },

  removeTodo: (id) => set((state) => ({ todos: state.todos.filter((t) => t.id !== id) })),

  setShowCompletedTodos: (value) => set({ showCompletedTodos: value }),

  deleteTodoRecord: (id) => set((state) => ({ completedTodoRecords: state.completedTodoRecords.filter((r) => r.id !== id) })),
});
