import type { StateCreator } from "zustand";
import type { AppState, SettingsSlice, BackgroundGradient } from "../types";
import { themePalettes, applyThemePalette, ThemePreset, ThemePalette } from "@/lib/themeConfig";
import { Language, setLanguage as setI18nLanguage } from "@/lib/i18n";

export const createSettingsSlice: StateCreator<AppState, [], [], SettingsSlice> = (set, get) => ({
  // Language
  language: "ko" as Language,
  setLanguage: (lang: Language) => {
    setI18nLanguage(lang);
    set({ language: lang });
  },

  // Auto Message
  autoMessageEnabled: false,
  setAutoMessageEnabled: (enabled) => set({ autoMessageEnabled: enabled }),
  autoMessagePreset: "regular" as const,
  setAutoMessagePreset: (preset) => set({ autoMessagePreset: preset }),

  // Theme
  selectedTheme: "default" as ThemePreset,
  colorOverrides: {},
  setSelectedTheme: (theme) => {
    set({ selectedTheme: theme });
    get().applyTheme();
  },
  setColorOverride: (key, value) => {
    set((state) => ({ colorOverrides: { ...state.colorOverrides, [key]: value } }));
    get().applyTheme();
  },
  resetColorOverrides: () => {
    set({ colorOverrides: {} });
    get().applyTheme();
  },
  applyTheme: () => {
    const { selectedTheme, colorOverrides } = get();
    applyThemePalette(themePalettes[selectedTheme], colorOverrides);
  },

  chatWallpaper: "default" as const,
  backgroundGradient: "none" as BackgroundGradient,
  setChatWallpaper: (wallpaper) => set({ chatWallpaper: wallpaper }),
  setBackgroundGradient: (gradient) => set({ backgroundGradient: gradient }),

  // Sound
  soundEnabled: true,
  soundVolume: 50,
  setSoundEnabled: (enabled) => set({ soundEnabled: enabled }),
  setSoundVolume: (volume) => set({ soundVolume: volume }),
});
