import type { AppState } from "./types";
import { ZodiacSign } from "@/lib/zodiac";
import { ItemCategory } from "@/lib/items";
import { zodiacPreferences } from "@/lib/zodiacPreferences";
import { ChallengeCondition } from "@/lib/challengesConfig";

export function generateId(): string {
  return `${Date.now().toString(36)}-${Math.random().toString(36).slice(2, 8)}`;
}

export const getDateString = () => new Date().toISOString().split("T")[0];

export function isPreferredItem(
  itemName: string,
  itemCategory: ItemCategory,
  zodiac: ZodiacSign | null
): boolean {
  if (!zodiac) return false;
  const prefs = zodiacPreferences[zodiac];
  if (!prefs) return false;

  // Map item category to preference array
  let prefArray: string[];
  switch (itemCategory) {
    case "food":
      prefArray = prefs.foods;
      break;
    case "place":
      prefArray = prefs.places;
      break;
    case "music":
      prefArray = prefs.music;
      break;
    case "gift":
      prefArray = prefs.giftItems;
      break;
    default:
      prefArray = [...prefs.foods, ...prefs.music, ...prefs.giftItems];
  }

  return prefArray.some((pref) => {
    const prefName = pref.replace(/[\u{1F300}-\u{1F9FF}]|[\u{2600}-\u{26FF}]|[\u{2700}-\u{27BF}]/gu, "").trim();
    return itemName.includes(prefName) || prefName.includes(itemName);
  });
}

export function isChallengeConditionMet(condition: ChallengeCondition, state: AppState): boolean {
  switch (condition.type) {
    case "pomodoro_count":
      return state.pomodoroSessionsCompleted >= condition.count;
    case "todo_count":
      return state.completedTodoRecords.length >= condition.count;
    case "chat_count":
      return state.chatMessageCount >= condition.count;
    case "points_earned":
      return state.totalPointsEarned >= condition.amount;
    case "affinity_reached":
      return state.characterProfile.affinity >= condition.level;
    case "gift_given":
      return state.giftGivenCount >= condition.count;
    case "consecutive_days":
      return state.consecutiveDays >= condition.days;
    case "emoji_unlocked":
      return state.emojis.filter((e) => e.unlocked).length >= condition.count;
    case "usage_minutes":
      return state.totalUsageMinutes >= condition.minutes;
    default:
      return false;
  }
}
