import type { StateCreator } from "zustand";
import type { AppState, PomodoroSlice, PomodoroRecord } from "../types";
import { generateId, getDateString } from "../helpers";
import type { CompletedTodoRecord } from "../types";
import { getAffinityTier } from "@/lib/completionReplies";
import { getCompletionReplyForTone } from "@/lib/toneCatalogs";
import { POINTS_CONFIG } from "@/lib/storeConfig";
import { notifyT } from "@/lib/i18n";

export const createPomodoroSlice: StateCreator<AppState, [], [], PomodoroSlice> = (set, get) => ({
  pomodoroMinutes: 25,
  pomodoroSeconds: 0,
  isPomodoroRunning: false,
  isBreakMode: false,
  pomodoroSessionsCompleted: 0,
  taskDuration: 25,
  breakDuration: 5,
  breakEnabled: true,
  autoStartBreak: true,
  pendingBreak: false,
  pomodoroMuted: false,
  pomodoroRecords: [],

  setPomodoroTime: (minutes, seconds) => set({ pomodoroMinutes: minutes, pomodoroSeconds: seconds }),
  setIsPomodoroRunning: (value) => set({ isPomodoroRunning: value }),
  setIsBreakMode: (value) => set({ isBreakMode: value }),
  setTaskDuration: (minutes) => set({ taskDuration: minutes }),
  setBreakDuration: (minutes) => set({ breakDuration: minutes }),
  setBreakEnabled: (enabled) => set({ breakEnabled: enabled }),
  setAutoStartBreak: (enabled) => set({ autoStartBreak: enabled }),
  setPomodoroMuted: (muted) => set({ pomodoroMuted: muted }),

  startBreak: () => {
    const breakDur = get().breakDuration;
    set({
      isBreakMode: true,
      pomodoroMinutes: breakDur,
      pomodoroSeconds: 0,
      isPomodoroRunning: true,
      pendingBreak: false,
    });
  },

  resetToFocus: () => {
    const taskDur = get().taskDuration;
    set({
      isBreakMode: false,
      pomodoroMinutes: taskDur,
      pomodoroSeconds: 0,
      isPomodoroRunning: false,
      pendingBreak: false,
    });
  },

  completePomodoroSession: () => {
    const newCount = get().pomodoroSessionsCompleted + 1;
    const sessionDuration = get().taskDuration;
    const record: PomodoroRecord = {
      id: generateId(),
      completedAt: getDateString(),
      timestamp: Date.now(),
      durationMinutes: sessionDuration,
    };
    set((state) => ({ pomodoroSessionsCompleted: newCount, pomodoroRecords: [...state.pomodoroRecords, record] }));
    const timerPoints = sessionDuration * POINTS_CONFIG.TIMER_PER_MINUTE;
    get().addPoints(timerPoints);
    get().addNotification(notifyT().focusRewardPoints(timerPoints), "pomodoro");
    const affinity = get().characterProfile.affinity;
    const tier = getAffinityTier(affinity);
    const tone = get().getCharacterTone();
    const playerName = get().playerProfile.name;
    const reply = getCompletionReplyForTone(tone, "pomodoro", tier, playerName);
    get().addCharacterMessage(reply);
    get().checkAllChallenges();
    get().markDailyAction("pomodoro");
    const breakEnabled = get().breakEnabled;
    const autoStartBreak = get().autoStartBreak;
    if (breakEnabled) {
      if (autoStartBreak) {
        get().startBreak();
      } else {
        set({ isPomodoroRunning: false, pendingBreak: true });
      }
    } else {
      set({
        isBreakMode: false,
        pomodoroMinutes: get().taskDuration,
        pomodoroSeconds: 0,
        isPomodoroRunning: false,
      });
    }
  },

  completeBreak: () => {
    set({
      isBreakMode: false,
      pomodoroMinutes: get().taskDuration,
      pomodoroSeconds: 0,
      isPomodoroRunning: false,
      pendingBreak: false,
    });
  },

  deletePomodoroRecord: (id) => set((state) => ({ pomodoroRecords: state.pomodoroRecords.filter((r) => r.id !== id) })),

  deleteRecordsByDate: (date) =>
    set((state) => ({
      pomodoroRecords: state.pomodoroRecords.filter((r) => r.completedAt !== date),
      completedTodoRecords: state.completedTodoRecords.filter((r) => r.completedAt !== date),
    })),
});
